.flat-accordion{max-width:740px;margin:0 auto}.flat-accordion .accordion-item{margin-bottom:21px;border-radius:0;background:rgba(34,183,143,.1);color:#fff;box-shadow:none;-webkit-transition:all .3s ease;-moz-transition:all .3s ease;-ms-transition:all .3s ease;-o-transition:all .3s ease;transition:all .3s ease;border:2px solid #105252;position:relative;z-index:1;padding:29px 23px 29px 19px}.flat-accordion .accordion-item:nth-last-child(1){margin-bottom:0}.flat-accordion .accordion-item::before,.flat-accordion .accordion-item::after{-webkit-transition:all .3s ease;-moz-transition:all .3s ease;-ms-transition:all .3s ease;-o-transition:all .3s ease;transition:all .3s ease;content:"";position:absolute;background:#0e2230;z-index:-1}.flat-accordion .accordion-item::before{width:calc(102% - 3rem);height:calc(100% + 4px);top:-2px;left:50%;transform:translateX(-50%)}.flat-accordion .accordion-item::after{height:calc(110% - 3rem);width:calc(100% + 4px);left:-2px;top:50%;transform:translateY(-50%)}.flat-accordion .accordion-item.active,.flat-accordion .accordion-item:hover{border-color:#14c2a3;box-shadow:0 0px 32px 0 rgba(34,183,143,.5)}.flat-accordion .accordion-item.active::before,.flat-accordion .accordion-item:hover::before{width:0 !important}.flat-accordion .accordion-item.active::after,.flat-accordion .accordion-item:hover::after{height:0 !important}.flat-accordion .accordion-item .accordion-button{position:relative;background:rgba(0,0,0,0);transform:translateY(0);padding:0;transition:all .3s ease;position:relative;z-index:1;border-radius:0 !important;color:#fff;box-shadow:none;font-size:24px;line-height:1.33;font-family:"Chakra Petch",sans-serif;font-weight:700;text-transform:capitalize;border-bottom:1px solid rgba(3,253,203,.1);padding-bottom:15px;margin-bottom:16px}.flat-accordion .accordion-item .accordion-button::after{background-image:none;content:"";font-family:"Font Awesome 5 Pro";font-weight:500;font-size:25px;color:#14c2a3;position:absolute;top:0;right:0;width:auto;height:auto}.flat-accordion .accordion-item .accordion-button.collapsed{border-bottom:1px solid rgba(3,253,203,0);padding-bottom:0;margin-bottom:0}.flat-accordion .accordion-item .accordion-collapse{background:rgba(0,0,0,0)}.flat-accordion .accordion-item .accordion-collapse .accordion-body{padding:0}.flat-accordion .accordion-item.corner-box{-webkit-transition:all .3s ease;-moz-transition:all .3s ease;-ms-transition:all .3s ease;-o-transition:all .3s ease;transition:all .3s ease;padding:28px 18px 11px}.flat-accordion .accordion-item.corner-box .toggle-title{-webkit-transition:all .3s ease;-moz-transition:all .3s ease;-ms-transition:all .3s ease;-o-transition:all .3s ease;transition:all .3s ease;border-bottom:1px solid rgba(3,253,203,0);padding-bottom:16px;margin-bottom:0;position:relative;padding-right:30px}.flat-accordion .accordion-item.corner-box .toggle-title::after{-webkit-transition:all .3s ease;-moz-transition:all .3s ease;-ms-transition:all .3s ease;-o-transition:all .3s ease;transition:all .3s ease;position:absolute;right:4px;top:0px;content:"";font-family:"Font Awesome 5 Pro";font-weight:500;font-size:25px;color:#14c2a3}.flat-accordion .accordion-item.corner-box .toggle-title:hover{cursor:context-menu}.flat-accordion .accordion-item.active.corner-box{padding:28px 18px}.flat-accordion .accordion-item.active.corner-box .toggle-title{margin-bottom:16px;border-bottom:1px solid rgba(3,253,203,.1)}.flat-accordion .accordion-item.active.corner-box .toggle-title::after{transform:rotate(-180deg)}